.information-container{display:flex;flex-wrap:wrap;height:100%}

.infromation-wrapper{display:flex;width:100%;align-items:center;justify-content:space-between;margin-bottom:3em}.information{max-width:30em;margin:2em;font-family:"Barlow";font-size:1.25em}.information .title{color:#2D6A4F}.information .description{margin-top:0.5em;color:#1B4332}

.about{height:50em;width:100%;position:inherit;padding:3em}

